export default{
	name:"zy-search",
	props:{
		isFocus:{	//是否自动获取焦点
			type:Boolean,
			value:false
		},
		isBlock:{	//选择块级显示还是圆形显示
			type:Boolean,
			value:true
		},
		showWant:{	//是否展示推荐菜单
			type:Boolean,
			value:false
		},
		hList:{		//历史记录
			type:Array,
			required:true,
			default:()=>[]
		}
	},
	data() {
		return {
			searchText:'',								//搜索关键词
			wantList:['栏目1','栏目2','栏目3','栏目4']	//初始化推荐列表
		};
	},
	methods: {
		searchStart: function() {	//触发搜索
			let _this = this;
			if (_this.searchText == '') {
				uni.showToast({
					title: '请输入关键字',
					icon: 'none',
					duration: 1000
				});
				return false;
			}else{
				uni.getStorage({
					key:'search_cache',
					success(res){
						let list = res.data;
						if(list.length > 5){
							for(let item of list){
								if(item == _this.searchText){
									return false;
								}
							}
							list.pop();
							list.unshift(_this.searchText);
						}else{
							for(let item of list){
								if(item == _this.searchText){
									return false;
								}
							}
							list.unshift(_this.searchText);
						}
						_this.hList = list;
						uni.setStorage({
							key: 'search_cache',
							data: _this.hList
						});
					},
					fail() {
						_this.hList.push(_this.searchText);
						uni.setStorage({
							key: 'search_cache',
							data: _this.hList
						});
					}
				})
			}
		},
		keywordsClick (item) {	//推荐搜索
			this.searchText = item;
		},
		delhistory () {		//清空历史记录
			this.hList = [];
			uni.setStorage({
				key: 'search_cache',
				data: []
			});
		},
		startRecognize: function() {	//语音输入
			let _this = this;
			let options = {};
			options.engine = 'iFly';
			options.punctuation = false; // 是否需要标点符号 
			options.timeout = 10 * 1000;
			plus.speech.startRecognize(options, function(s) {
				_this.searchText = _this.searchText + s;
			});
		}
	}
}
